@import 'Mixin';

.order_tabs{
    @include whb(100%, 1840px);

    .menu_tabs{
        @include whb(100%, 120px);
        display: flex; flex-wrap: wrap;

        button{
            flex: 1 1 calc(33.333% - 10px); /* 버튼의 너비를 3등분하고 간격을 고려 */
            box-sizing: border-box; /* 패딩과 테두리를 너비에 포함 */
            color:#49120687; cursor:pointer; background:none;
            font-size:18px; font-family: "Cafe24Ssurround";
            position:relative;
            
        }
        .selected{
            color:red;
            
            span{
                position:absolute;
                width:100%;
                height:3px;
                bottom:-1px;
                left:0;
                background:red;
            }
        }
        .bo{
            border-bottom:1px solid #ccc;
        }
        .ri{
            border-right:1px solid #ccc;
        }
    }
    .bug_tabs{
        @include whb(100%, 800px);
        

        &>article{
            @include whb(360px, 360px); float:left;
            
            ul{
                @include flex(center,center);
                flex-direction:column; cursor:pointer;
                li{
                    width:100%;
                    .item_img{
                        margin:0 auto;
                        @include whb(300px, 224.34px);
                    }
                }
                .item_name{
                    margin-top:20px;
                    p{
                        text-align:center;
                        color:#5B1607;
                        font-size:22px;
                    }
                }
                .item_price{
                    margin-top:35px;
                    p{
                        color:red; 
                        text-align: center;
                        font-size:22px;
                    }
                }
            }
        }

        .addItem_wrap{
            position:absolute;
            top:0;
            left:0;
            @include whb(1080px, 1920px);
            background:rgba(0, 0, 0, 0.663);
            @include flex(center,center);
            z-index:3;

            .addItem{
                @include whb(800px, auto,#fff);
                margin-top:-500px;
                border-radius:20px;
                padding:40px;

                .modal_wrap{
                    @include whb(100%, 100%); position: relative;
                    display:flex; flex-direction: column;

                    .close_btn{
                        @include whb(28px, 28px,url("./images/close_btn.png")no-repeat center center);
                        background-size:cover; cursor:pointer; position:absolute; right:30px; top:3px;
                    }
                   .h2_wrap{
                        border-bottom:2px dashed #E2CFC9;
                        padding-bottom:20px;
                    
                        h2{
                            color:#591506;
                            text-align: center;
                            font-size:30px;
                        }
                    } 
                
                    .select_wrap{
                        display:flex;
                        flex-direction: column; cursor:pointer;

                        article{
                            @include flex(space-around,center);
                            
                            .a1{
                                @include whb(80px, 224.34px);
                                @include flex(center,center);
                                
                                label {
                                    display:block;
                                    @include whb(30px, 30px);
                                    position: relative;
                                    padding: 10px;
                                    cursor: pointer;
                                    background-color: #e4e4e4;
                                    border-radius: 8px;

                                    .check{
                                        display:block; @include whb(28px, 28px,url("./images/check.png")no-repeat center center);
                                        background-size:cover; position: absolute; left:11px;
                                    }
                                    input{
                                        display: none; /* 기본 라디오 버튼 숨김 */
                                    }
                                    
                                }
                                label.select{
                                    background-color: #491206; /* 선택된 경우 색상 */
                                    color: white; /* 텍스트 색상 변경 */
                                }              
                            }
                            
                            .a2{
                                @include whb(300px, 224.34px);
                                @include flex(center,); flex-direction: column;
                                gap:7px;
                                h3{
                                    color:#591506;
                                    font-weight:bold;
                                    font-size:28px;
                                }
                                p{
                                    color:#591506;
                                    font-size:18px;
                                }
                                .price{
                                    color:red;
                                    font-size:28px;
                                }                
                            }
                            .a3{
                                @include whb(300px, 224.34px);
                                @include flex(center,center); 
                                img{
                                    width:300px;
                                    height:224.34px;
                                    background-size:cover;
                                    float:right;
                                }               
                            }
                            
                        }
                    }

                    .btn_wrap{
                        margin-top:80px;
                        button{
                            @include whb(700px, 120px); border-radius:80px;
                            color:#fff; font-size:36px; cursor:pointer;
                        }
                        .disabled {
                            background: #e4e4e4; /* 회색 */
                            cursor: not-allowed; /* 클릭 불가 커서 */
                        }
                        
                        .active {
                            background: red; /* 빨간색 */
                            cursor: pointer; /* 클릭 가능 커서 */
                        }
                    }
                }
            }
        }
    }
    .cart_tabs{
        @include whb(100%, 500px,#4912062a);
        position:absolute; bottom:0;

        .cart_wrap{
            @include whb(100%, auto);
            padding:30px; display:block; padding-bottom:0;
            display:flex; flex-direction: column;

            .d1{
                @include whb(100%, 50px);

                p{
                    position:absolute; font-size:30px; display:inline;
                    color:#491206;
                }
                .guide{
                    top:80px;
                    font-size:20px; color:rgb(49, 49, 49);
                }
                .price{
                    right:30px;
                }
            }
            .d2{
                @include whb(100%, 300px); 

                .empty{
                    color:#49120675; font-size:25px;
                    text-align: center; margin-top:105px;
                }
                .menuList{
                    @include whb(100%, 300px);
                    overflow-x: auto; white-space: nowrap;

                    ul{
                    @include whb(1020px, 300px); box-sizing: border-box;
                    overflow-x: auto; padding-right:50px;

                        li{
                            @include whb(300px, 160px,#fff);
                            display:inline-block; margin-left:50px; margin-top: 70px; border-radius:20px;
                            position:relative;

                            .delete_btn{
                                @include whb(22px, 22px,url("./images/close_btn.png")no-repeat center center);
                                background-size:cover; cursor:pointer; position:absolute; right:15px; top:15px;
                            }

                            .b_name{
                                color:#491206;
                                position:absolute; font-size:23px; left:13px; top:20px;
                            }
                            .b_price{
                                color:red; position:absolute; font-size:29px; left:13px; bottom:15px;
                            }
                        }
                    }
                    ul::-webkit-scrollbar {
                        height: 10px; /* 수평 스크롤바의 높이 */
                    }
                    
                    ul::-webkit-scrollbar-thumb {
                        background-color: #4912069f; /* 스크롤바 막대 색상 */
                        border-radius: 5px; /* 모서리 둥글게 */
                    }
                    
                    ul::-webkit-scrollbar-track {
                        background: rgba(255, 255, 255, 0.2); /* 스크롤바 트랙 배경 색상 (선택 사항) */
                    }
                }
                
            }
            .d3{
                @include whb(100%, 100px);
                @include flex(space-around,center);

                button{
                    @include whb(300px, 80px); border-radius:50px;
                    color:#fff; font-size:25px; cursor:pointer;
                }
                .btn_1{
                    background:#491206;
                }
                .btn_2{
                    background:red;
                }
                .btn_1.disabled,
                .btn_2.disabled {
                    background-color: gray; /* 비활성화 상태의 배경색 */
                    color: white; /* 비활성화 상태의 글자색 */
                    cursor: not-allowed; /* 마우스 커서 변경 */
                }
            }
        }
    }
    .payment_wrap{
        position:absolute;
        top:0;
        left:0;
        @include whb(1080px, 1920px);
        background:rgba(0, 0, 0, 0.663);
        @include flex(center,center);
        z-index:3;

            .payment{
                @include whb(800px, auto,#fff);
                margin-top:-500px;
                padding:40px;

                .modal_wrap{
                    @include flex(center,center);
                    flex-direction: column;

                    .h2_wrap{
                        width:100%;
                        border-bottom:1px solid #49120694;
                        padding-bottom:20px;

                        h2{
                            text-align: center;
                            color:#491206;
                        }
                    }
                    .select_wrap{
                        @include whb(100%, 400px);
                        @include flex(center,center);
                        padding-top:30px;

                        div{
                            flex: 1;
                            @include whb(100%, 100%);
                            position:relative;
                            cursor:pointer;
                            

                            .icon{
                                @include whb(260px, 260px);
                                margin:0 auto;
                            }
                            p{
                                margin-top:30px;
                                text-align: center;
                                font-size:30px;
                                color:#491206;
                            }
                            .check{
                                font-size:160px;
                                color:red;
                                top:20px;
                                left:125px;
                                position:absolute;
                            }
                        }
                        .here{
                            div{
                                background:url('./images/choose_1.png')no-repeat center center;
                                background-size:cover;
                            }
                        }
                        .takeout{
                            div{
                                background:url('./images/choose_2.png')no-repeat center center;
                                background-size:cover;
                            }
                        }
                    }
                    .btn_wrap{
                        @include whb(100%, 100px);
                        @include flex(center,center);

                        button{
                            @include whb(100%, 80px); border-radius:50px;
                            color:#fff; font-size:25px;
                        }
                        .disabled {
                            background: #e4e4e4; /* 회색 */
                            cursor: not-allowed; /* 클릭 불가 커서 */
                        }
                        
                        .active {
                            background: #491206; /* 빨간색 */
                            cursor: pointer; /* 클릭 가능 커서 */
                        }
                    }
                }
            }
    }
    .printing_wrap{
        position:absolute;
        top:0;
        left:0;
        @include whb(1080px, 1920px);
        background:rgba(0, 0, 0, 0.663);
        @include flex(center,center);
        z-index:3;

        .printing{
            @include whb(800px, auto,#fff);
            margin-top:-500px;
            padding:40px;

            .modal_wrap{
                @include flex(center,center);
                flex-direction: column;
                padding-top:30px;

                .h2_wrap{
                    width:100%;
                    padding-bottom:20px;

                    h2{
                        text-align: center;
                        color:#491206;
                        font-size:32px;
                    }
                }
                .select_wrap{
                    @include whb(100%, 200px);
                    @include flex(center,center);
                    padding-top:30px;

                    div{
                        flex: 1;
                        @include whb(100%, 100%);
                        @include flex(center,center);

                        button{
                            @include whb(90%, 100px);
                            font-size:30px; color:#fff;
                            border-radius:50px; cursor:pointer;
                        }
                    }
                    .receipt{
                        button{
                            background:#491206;
                        }
                    }
                    .number{
                        button{
                            background:#ff2f00;
                        }
                    }
                }
            }
        }
    }
    .method_wrap{
        position:absolute;
        top:80px;
        left:0;
        @include whb(1080px, 1840px,rgb(237, 232, 217));
        z-index:3;

        .h2_wrap{
            margin-top:30px;

            h2{
                text-align:center;
                font-size: 50px;

                span{
                    color:red;
                }
            }
        }

        .select_wrap{
            @include whb(100%, 600px);
            margin-top:30px; @include flex(space-between,center);
            gap:70px;  flex-wrap: wrap; padding:20px 100px;

            &>div{
                @include whb(400px, 350px,#fff);
                border-radius:25px; @include flex(center,center);
                flex-direction: column; cursor:pointer;

                .img{
                    @include whb(200px, 200px);
                }
                p{
                    color:#491206;
                    font-size:30px;
                }

            }

            .card{
                .img{
                    background:url('./images/credit.png')no-repeat center center;
                    background-size:cover;
                }
            }
            .payco{
                .img{
                    background:url('./images/payco.png')no-repeat center center;
                    background-size:cover;
                }
            }
            .kakao{
                .img{
                    background:url('./images/kakaopay.png')no-repeat center center;
                    background-size:cover;
                }
            }

        }

        .price_wrap{
            position:absolute;
            @include whb(1080px, 400px,rgb(255, 255, 255));
            bottom:0;
            left:0;
            @include flex(center,center);
            flex-direction: column;

            .price{
                @include whb(100%, 100px);
                @include flex(space-around,center);

                .total{
                    color:#491206;
                    font-size:40px;
                }

                .total_price{
                    color:red;
                    font-size:42px;
                }
            }
            .btn_wrap{
                @include whb(100%, 300px);
                @include flex(center,center);

                button{
                    @include whb(90%, 150px,#491206);
                    color:#fff;
                    font-size:40px;
                    border-radius:100px;
                    cursor:pointer;
                }
            }
        }
    }

    .cardpayment_wrap{
        position:absolute;
        top:80px;
        left:0;
        @include whb(1080px, 1840px,rgb(237, 232, 217));
