            }
        }

        .select_wrap{
            @include whb(100%, 1000px);
            margin-top:100px; @include flex(center,center);

            .credit_img{
                @include whb(800px, 800px,url('./images/cardpayment.png')no-repeat center center);
                background-size:cover;
            }

        }

        .price_wrap{
            position:absolute;
            @include whb(1080px, 300px,rgb(255, 255, 255));
            bottom:0;
            left:0;
            @include flex(center,center);
            flex-direction: column;

            .price{
                @include whb(100%, 100px);
                @include flex(space-around,center);

                .total{
                    color:#491206;
                    font-size:40px;
                }

                .total_price{
                    color:red;
                    font-size:42px;
                }
            }
        }
    }

    .qrpayment_wrap{
        position:absolute;
        top:80px;
        left:0;
        @include whb(1080px, 1840px,rgb(237, 232, 217));
        z-index:3;

        .h2_wrap{
            margin-top:200px;

            h2{
                text-align:center;
                font-size: 60px;
                color:#491206;

                span{
                    color:red;
                }
            }

            p{
                text-align: center;
                font-size:30px;
                margin-top:50px;
            }

            .notif{
                color:blue;
            }
        }

        .select_wrap{
            @include whb(100%, 1000px);
            margin-top:100px; @include flex(center,center);

            .credit_img{
                @include whb(400px, 400px,url('./images/scan.png')no-repeat center center);
                background-size:cover;
            }

        }

        .price_wrap{
            position:absolute;
            @include whb(1080px, 300px,rgb(255, 255, 255));
