    .completed_wrap{
        position:absolute;
        top:80px;
        left:0;
        @include whb(1080px, 1840px,#fff);
        z-index:3;

        .completed{
            margin-top:250px; text-align:center;

            h2{
                font-size: 60px;
                color:#491206;
            }

            .instructions_number{
                color:red;
                font-size:40px;
                margin-top:100px;

                .number{
                    font-size:100px;
                }
            }
            .instructions{
                font-size:35px;
                margin-top:80px;
            }

            .printImg{
                @include whb(400px, 400px,url('./images/printing.png')no-repeat center center);
                background-size:cover;
                margin:200px auto;
            }
        }
    }

    .receipt_wrap{
        font-family: 'Pretendard-Regular';
        font-size:22px;
        position:absolute;
        top:0;
        left:0;
        @include whb(1080px, 1920px,#000000e2);
        z-index:3;
        @include flex(center,center);

        .receipt{
            @include whb(800px, auto,#ffffff);
            padding:40px 30px; @include flex(center,center);
            flex-direction: column; gap:3px; position:relative;

            .close_btn{
                @include whb(30px, 30px,url('./images/close_btn.png')no-repeat center center);
                background-size:cover; cursor:pointer; position:absolute; right:10px; top:10px;
            }
            &>div{
                @include whb(100%, auto);
                padding:5px 0;
            }
            .top{
                border-bottom:1px dashed #000;
            }
            .num{
                font-size:30px; border-bottom:1px dashed #000;
                border-top:1px dashed #000; text-align: center;
                font-weight:bold;
            }
            .address{
                border-bottom:1px dashed #000;
                border-top:1px dashed #000;
            }
            .tel{
                border-bottom:1px dashed #000;
                border-top:1px dashed #000; font-weight:bold;
            }
            .list{
                border-bottom:1px dashed #000;
                border-top:1px dashed #000;

                ul{
                    @include whb(100%, 100%);

                    p{
                        display:inline-block;
                    }
                    .price{
                        float:right;
                    }
                }
            }
            .cost{
                border-bottom:1px dashed #000;

                p{
                    @include whb(100%, 100%);
                    @include flex(space-between,center);
                }
            }
            .manager{
                border-bottom:1px dashed #000;
            }
            .toilet{
                border-top:1px dashed #000;
                border-bottom:1px dashed #000;
            }
            .event{
                border-top:1px dashed #000;
                border-bottom:1px dashed #000;

                .code{
                    text-align: center;
                    width:auto;
                    font-size:30px;
                }
            }
            .barcode{
                @include whb(250px, 80px,url('./images/barcode.png')no-repeat center center);
                background-size:cover; margin-top:30px;
            }
        }
    }

    .orderNumber_wrap{
        font-family: 'Pretendard-Regular';
        font-size:22px;
        position:absolute;
        top:0;
        left:0;
        @include whb(1080px, 1920px,#000000e2);
        z-index:3;
        @include flex(center,center);

        .orderNumber{
            @include whb(800px, auto,#ffffff);
            padding:40px 30px; @include flex(center,center);
            flex-direction: column; gap:3px; position:relative;

            .close_btn{
                @include whb(30px, 30px,url('./images/close_btn.png')no-repeat center center);
                background-size:cover; cursor:pointer; position:absolute; right:10px; top:10px;
            }
