@import 'Mixin';

.Initialscreen_wrap{
    @include whb(100%, 100%,#fff);
    position:absolute;
    z-index:3;
    @include flex(center,center);
    cursor:pointer;
    flex-direction: column;
    gap:400px;
    color:#491206;

    .logo{
        position:absolute;
        @include whb(80px, 80px,url('./images/logo.png')no-repeat center center);
        background-size:cover;
        top:15px;
        left:15px;
    }
    .h1_wrap{
        width:800px;
        border-top:8px solid #491206;
        border-bottom:8px solid #491206;
        padding:20px 0;
        margin-top:-150px;
        
        h1{
            font-size:40px;
            text-align:center;
        }
    }
    .explanation_wrap{
        margin-top:50px;
        width:800px;
        display:flex;
        flex-direction:column;
        list-style: disc;
        gap:20px;

        .red{
            color:red;
        }
        li{
            width:800px;
            font-size:25px;
            line-height:1.3;
        }
    }
    .click_box{
        @include whb(100%, 500px,#491206);
        position:absolute;
        bottom:0; @include flex(center,center);

        p{
            color:#fff;
            font-size:50px;
            animation: blink 3s infinite;
        }
        @keyframes blink {
            0% {
                opacity: 1;
            }
            50% {
                opacity: 0;
            }
            100% {
                opacity: 1;
            }
        }
    }
    
}